
export default class DrawGeoJSON{
	constructor(){
	}

	draw(id, feature){
		const d3 = require('d3');
		function draw_geojson_feature_in(id, feature){
			var data = feature["geometry"]["coordinates"];
			var div = document.getElementById(id);
			div.innerHTML = "";
			var xMin = Infinity;
			var yMin = Infinity;
			var xMax = 0;
			var	yMax = 0;
			var width = div.offsetWidth;
			var height = div.offsetHeight;
		
			for(const feature of data){
				for(let index = 0; index < feature.length; index++){
					if(feature[index][0] < xMin) xMin = feature[index][0];
					if(feature[index][1] < yMin) yMin = feature[index][1];
					
					if(feature[index][0] > xMax) xMax = feature[index][0];
					if(feature[index][1] > yMax) yMax = feature[index][1];
				}
			}
		
			var xRange = d3.scaleLinear()
				.range([width, 0])
				.domain([xMax, xMin]);
			var yRange = d3.scaleLinear()
				.range([0, height])
				.domain([yMax, yMin]);
			
			var svg = d3.select("#"+id).append("svg")
				.attr("width", width)
				.attr("height", height);
		
			svg.selectAll("polygon").data(data).enter()
				.append("polygon")
				.attr("points", function(d){
					return d.map(function(d){
						return [xRange(d[0]), yRange(d[1])].join(",");
					}).join(" ");
				})
				.attr("stroke", '#000000')
				.attr("stroke-width", 2)
				.attr("fill", "white");
		}

		window.addEventListener('resize', function(){
			draw_geojson_feature_in(id, feature);
		});
		draw_geojson_feature_in(id, feature);
	}
}
