/**
 * @module M/impl/control/PLG_Seleccion_AreaControl
 */

export default class PLG_Seleccion_AreaControl extends M.impl.Control {
  /**
   * @classdesc
   * Main constructor of the PLG_Seleccion_AreaControl.
   *
   * @constructor
   * @extends {M.impl.Control}
   * @api stable
   */
  constructor() {
    super();
  }
  /**
   * This function adds the control to the specified map
   *
   * @public
   * @function
   * @param {M.Map} map to add the plugin
   * @param {HTMLElement} html of the plugin
   * @api stable
   */
  addTo(map, html) {
    super.addTo(map, html);
    this.map_ = map;
  }

  /**
   * Crea interacción de dibujo
   * @function
   * @param {M.layer} capa - this.capaDeDibujo de la fachada
   */
  creaInteraccion(capa) {
    return new ol.interaction.Draw({
      source: capa.getImpl().getOL3Layer().getSource(),
      type: 'Circle',
      geometryFunction: ol.interaction.Draw.createBox()
    });
  }

  /**
   * Agrega la interacción de dibujo al mapa.
   * @function
   * @param {ol.interaction.Draw} interaccion - this.draw de la fachada
   */
  agregaInteraccion(interaccion) {
    this.map_.getMapImpl().addInteraction(interaccion);
  }

  /**
   * Elimina la interacción de dibujo al mapa.
   * @function
   * @param {ol.interaction.Draw} interaccion - this.draw de la fachada
   */
  eliminaInteraccion(interaccion) {
    this.map_.getMapImpl().removeInteraction(interaccion);
  }

  /**
   * Añade el evento de eliminación de feature tras añadirse a la capa de dibujo.
   * @function
   * @param {M.layer} capa - this.capaDeDibujo de la fachada
   */
  eliminaFeatureTrasDibujo(capa) {
    capa.getImpl().getOL3Layer().getSource().on('addfeature', f => {
      capa.getImpl().getOL3Layer().getSource().removeFeature(f.feature);
    });
  }

  /**
   * Halla el centro del extent de una feature.
   * @function
   * @param {OL Feature} feature - Bbox dibujada con interacción draw
   */
  getCentroBbox(feature) {
    return ol.extent.getCenter(feature.getGeometry().getExtent());
  }
}