/**
 * @module M/control/PLG_Seleccion_EspacialControl
 */ 

import PLG_Seleccion_EspacialImplControl from 'impl/plg_seleccion_espacialcontrol';
import $ from 'jquery';
import Handlebars from 'handlebars/lib/handlebars';
import DrawGeoJSON from './DrawGeoJSON';
import 'regenerator-runtime';
import templ from 'templates/plg_seleccion_espacial';
import divHTML from 'templates/templateDiv.html';
import popupTemplate from 'templates/popup.html';

export default class PLG_Seleccion_EspacialControl extends M.Control {

  /**
   * @classdesc
   * Main constructor of the class. Creates a PluginControl
   * control
   *
   * @constructor
   * @extends {M.Control}
   * @api stable
   */
  constructor(userParams) {
    // 1. checks if the implementation can create PluginControl
    if (M.utils.isUndefined(PLG_Seleccion_EspacialImplControl)) {
      M.exception('La implementación usada no puede crear controles PLG_Seleccion_EspacialControl');
    }
    // 2. implementation of this control
    let impl = new PLG_Seleccion_EspacialImplControl();
    super(impl, "PLG_Seleccion_Espacial");
    this.userParams = userParams;

    this.capaParcelas_ = null; //Capa de las parcelas
    this.map_ = null; //Mapa general
    this.div_ = null; //Div del panel
    this.parcelaSeleccionada_ = null; //Parcela seleccionada
    
    this.layerToShowWhileHovering; // Capa con las geometrías que se muestran a modo de previsualización
    this.layerSelectedFeature; // Capa con la geometría seleccionada
    this.originalStylePoints; // Estilos del cluster al cargar el plugin
    this.layerParcelaSeleccionada; // Capa con las parcelas seleccionadas
    this.pointSelected; // Punto seleecionado, si se ha seleccionado uno. Solo sirve para el zoom

    this.styleSelectedPolygon = new M.style.Polygon({
      fill: {
        color: 'yellow'
      }
    });
    this.styleSelectedPolygonType = "fill";


    if (!M.template.compileSync){
      M.template.compileSync = (string, options) => {
        let templateCompiled;
        let templateVars = {};
        let parseToHtml;
        if (!M.utils.isUndefined(options)){
          templateVars = M.utils.extends(templateVars, options.vars);
          parseToHtml = options.parseToHtml;
        }
        const templateFn = Handlebars.compile(string);
        const htmlText = templateFn(templateVars);
        if (parseToHtml !== false) {
          templateCompiled = M.utils.stringToHtml(htmlText);
        } else {
          templateCompiled = htmlText;
        }
        return templateCompiled;
      };
    }

  }
  /**
   * This function creates the view
   *
   * @public
   * @function
   * @param {M.Map} map to add the control
   * @api stable
   */
   createView(map) {
    return new Promise((success, fail) => {
      const html = M.template.compileSync(templ);
      this.map_ = map;
      success(html);
    });
   }
  /**
   * This function is called on the control activation
   *
   * @public
   * @function
   * @api stable
   */
  activate() {
    super.activate(); //calls super to manage de/activation
    var _this = this;

    /*
      Creamos las capas temporales
    */
    // Estilo original de la capa de puntos
    this.originalStylePoints = this.getCapaPuntos().getStyle();
    // Capa que remarca las capas al pasar el ratón por encima del popup
    this.layerToShowWhileHovering = new M.layer.GeoJSON({
      name: "layerToShowWhileHovering",
      crs: 'EPSG:25830',
      extract: false
    });
    this.layerToShowWhileHovering.setStyle(new M.style.Polygon({
      fill: {}
    }));
    this.layerToShowWhileHovering.setZIndex(9999999);
    // Capa que señala la capa seleccionada
    this.layerSelectedFeature = new M.layer.GeoJSON({
      name: "layerSelectedFeatureInfoWFS",
      crs: 'EPSG:25830',
      extract: false
    });
    this.layerSelectedFeature.setStyle(this.styleSelectedPolygon);
    this.layerSelectedFeature.setZIndex(5000);
    // Capa que señala la parcela seleccionada
    this.layerParcelaSeleccionada = new M.layer.GeoJSON({
      name: 'layerParcelaSeleccionada',
      crs: 'EPSG:25830',
      extract: false
    });
    this.layerParcelaSeleccionada.setStyle(new M.style.Polygon({
      fill: {
        color: "#00ffff"
      }
    }));
    this.layerParcelaSeleccionada.setZIndex(7000);
    this.map_.addLayers(this.layerToShowWhileHovering);
    this.map_.addLayers(this.layerSelectedFeature);
    this.map_.addLayers(this.layerParcelaSeleccionada);

    // Evento principal
    this.mainEvent = async (evt)=>{
      // Función de Handlebars. Pone la primera letra del texto en mayúscula
      Handlebars.registerHelper('capitalizeFirst', (str)=>{
        var res = str.toLowerCase();
        res = res.charAt(0).toUpperCase() + res.slice(1);
        return res;
      });
      
      if(_this.map_.getPopup() != null) _this.map_.removePopup();
      _this.layerToShowWhileHovering.removeFeatures(_this.layerToShowWhileHovering.getFeatures());
      var features = [];

      // Busca los seleccionados en la capa
      var selected = _this.map_.getFeatureHandler().prevSelectedFeatures_[_this.userParams.nombreCapaPuntuales];
      // Comprueba si en lo seleccionado hay un objeto clúster.
      // Si no lo hay, se ha clickado en una feature, por lo que se mete en el array de features
      // Si lo hay, y en ese cluster hay menos 3, se obtienen las features del clúster y se meten en el array
      var filtered = selected.find(x => x instanceof M.ClusteredFeature);
      if(filtered == null && selected.length > 0){
        features = features.concat(selected);
      }else if(filtered != null){
        if(filtered.getGeoJSON().properties.features.length > 3) return;
        features = features.concat(filtered.getGeoJSON().properties.features);
      }

      // Hacemos las peticiones getFeatureInfo de las capas WMS, obtenemos las geometrías y las guardamos en el array
      // TODO Zona importante de cambio para transformación de múltiples capas a una
      features = features.concat(await _this.makeWMSPetition(evt));
      if(features.length == 0) return;
      _this.layerToShowWhileHovering.addFeatures(features.filter(x=>x.getGeometry().type == "Polygon"));

      // Creamos las variables para la plantilla
      var context = {feature: []};
      features.forEach(f=>{
        context.feature.push({
          id: f.getGeoJSON().properties.N_INVENTARIO,
          matricula: f.getGeoJSON().properties.C_CODIGO,
          tipologia: f.getGeoJSON().properties.TIPO_BIEN,
          tipo: f.getGeoJSON().geometry.type,
          n_activo_giro: f.getGeoJSON().properties.N_ACTIVO_GIRO,
          denominacion: f.getGeoJSON().properties.D_DENOMINACION
        });
      });
      
      // Compilamos la plantilla
      var templ = Handlebars.compile(popupTemplate);
      var popupobjtxt = templ(context);
      // Creamos y añadimos el popup
      var featureTabOpts = {
        'icon': 'g-cartografia-pin',
        'title': 'Features',
        'content': popupobjtxt
      };
      var popup = new M.Popup();
      popup.addTab(featureTabOpts);
      _this.map_.addPopup(popup, evt.coord); 

      // Añadimos los eventos al popup
      setTimeout(()=>{
        if(_this.map_.getPopup() != null){
          _this.addEventsHover(_this.map_.getPopup().getContent());
          _this.addEventsClick(_this.map_.getPopup().getContent());
        }
      }, 200);
    }
    // Añadimos el evento al mapa
    this.map_.on(M.evt.CLICK, this.mainEvent);
  }


  /**
   * This function is called on the control deactivation
   *
   * @public
   * @function
   * @api stable
   */
  deactivate() {
    super.deactivate(); //calls super to manage de/activation
    // Quitamos el evento
    this.map_.un(M.evt.CLICK, this.mainEvent);

    // Borramos todas las capas virtuales
    this.layerSelectedFeature.removeFeatures(this.layerSelectedFeature.getFeatures());
    this.layerToShowWhileHovering.removeFeatures(this.layerToShowWhileHovering.getFeatures());
    this.layerParcelaSeleccionada.removeFeatures(this.layerParcelaSeleccionada.getFeatures());
    this.map_.removeLayers([this.layerSelectedFeature, this.layerToShowWhileHovering, this.layerParcelaSeleccionada]);

    // Reiniciamos los estilos de las capas puntos
    if(this.getCapaPuntos().isVisible()){
      this.getCapaPuntos().setStyle(this.originalStylePoints, true);
    }

    this.borraParcela();
    // Borramos el popup si lo hay
    if(this.map_.getPopup() != null){
    	this.map_.removePopup();
    }
    // Borramos el div si lo hay
    if(this.div_ != null){
      this.div_.parentNode.removeChild(this.div_);
      this.div_ = null;
    }
    
  }
   /**
   * This function gets activation button
   *
   * @public
   * @function
   * @param {HTML} html of control
   * @api stable
   */
  getActivationButton(html) {
    return html.querySelector('.m-plg_seleccion_espacial button');
  }
  
  /**
   * This function compares controls
   *
   * @public
   * @function
   * @param {M.Control} control to compare
   * @api stable
   */
  equals(control) {
    return control instanceof PLG_Seleccion_EspacialControl;
  }

  /**
   * @public
   * @function
   * @param {Event} evt Evento con las coordenadas donde se hace la petición.
   * Función que realiza la petición de getFeatureInfo en las capas indicadas en el "this.wmsLayers"
   */
  async makeWMSPetition(evt){
    var _this = this;
    var features = [];
    var layers=[];
    // Buscamos entre las capas pasadas si son visibles o no para hacer la petición
    for(const layerName of this.userParams.wmsLayers){
      var l = this.map_.getLayers().find(g => g.name === layerName);
      if(l === null) continue;
      if(l.isVisible()) layers.push(l);
    }
    
    // Creamos las promesas que se encargan de hacer la petición
    var promises = [];
    layers.forEach(l=>{
      promises.push(new Promise((resolve, reject) => {
        const getFeatureInfoParams = {
          INFO_FORMAT: 'geojson',
          FEATURE_COUNT: 10
        };
        const source = l.getImpl().getOL3Layer().getSource();
        const coord = evt.coord;
        const srs = _this.map_.getProjection().code;
        const viewResolution = _this.map_.getMapImpl().getView().getResolution();
        const url = source.getGetFeatureInfoUrl(coord, viewResolution, srs, getFeatureInfoParams);
        M.remote.get(url).then((data) => {resolve(data)}).catch(e=>reject('{"text": {"features":[]}}'));
      }))
    });

    // Obtenemos los resultados una vez todas las promesas se hayan resuelto
    var values = await Promise.all(promises);
    values.forEach(v => {
      try{
        // TODO Posible punto de cambio en multiples capas a una
        JSON.parse(v.text).features.forEach(f=>{
          if(_this.capaUnica == null || !_this.userParams.capaUnica){
            f.properties["TIPO_BIEN"] = _this.userParams.capaATipo[JSON.parse(v.text).name];
          }
          features.push(new M.Feature('featIgbdpBien'+f.properties.N_INVENTARIO, f));
        });
      }catch(e){}
    });
    return features;
  }

  /**
   * @public
   * @function
   * @param {HTMLElement} content Contenido del popup.
   * Función que añade los eventos de cambio de simbología al pasar el ratón por encima
   */
  addEventsHover(content){
    var _this = this;
    var featurePolygon;
    var featurePoint;
    var stylePoint;
    // Cuando pasamos el ratón por encima de una fila con clase rowDataPopup que esté en el popup
    $(content).find(".rowDataPopup").hover((e)=>{
      var id = e.currentTarget.dataset.id;
      var tipo = e.currentTarget.dataset.tipo;
      if(tipo == "Polygon"){
        // TIPO POLIGONO
        featurePolygon = _this.layerToShowWhileHovering.getFeatures().find(x=>x.getGeoJSON().properties.N_INVENTARIO == id);
        if(featurePolygon != null){
          featurePolygon.setStyle(new M.style.Polygon({
            fill: {
              color: '#00ffbb',
              opacity: 0.5
            }
          }));
        }
      } else {
        // TIPO PUNTO
        featurePoint = _this.getCapaPuntos().getFeatures().find(x => x.getGeoJSON().properties.N_INVENTARIO == id);
        var iconData = _this.userParams.iconoPuntoSeleccionado[featurePoint.getGeoJSON().properties.CODIGO_TIPO_BIEN];
        stylePoint = featurePoint.getStyle();
        featurePoint.setStyle(new M.style.Point({}));
        featurePoint.setStyle(new M.style.Point({
          icon: {
            src: iconData.url,
            scale: iconData.scale
          }
        }));
      }
    }, ()=>{
      // Función para cuando quitamos el ratón de encima (set default styles)
      if(featurePolygon != null){
        featurePolygon.setStyle(new M.style.Polygon({fill:{}}));
        featurePolygon = null;
      }
      if(featurePoint != null){
        featurePoint.setStyle(stylePoint);
        featurePoint = null;
        stylePoint = null;        
      } 
    });
  }

  /**
   * @public
   * @function
   * @param {HTMLElement} content Contenido del popup.
   * Función que añade los eventos de ver históricos al clickar el botón
   */
  addEventsClick(content){
    var _this = this;
    $(content).find(".button_info_ver_mas").click((evt)=>{
      var id = evt.currentTarget.dataset.id;
      var tipo = evt.currentTarget.dataset.tipo;
      _this.creaDivConFeature(id, tipo);
    });
  }

  /**
   * @public
   * @function
   * Busca y obtiene la capa de puntos
   */
  getCapaPuntos(){
    return this.map_.getLayers().find(x => x.name === this.userParams.nombreCapaPuntuales);
  }

  /**
   * @public
   * @function
   * @param {Integer} featureID Número de inventario
   * Busca el tipo de geometria por el N_INVENTARIO
   */
  async getTipoPorId(n_inventario){
    var _this = this;
    function peticionPoligono(n_inventario){
      return $.ajax({
        url: _this.userParams.capa_wfs_bienes + "&filter=<Filter><PropertyIsEqualTo><PropertyName>N_INVENTARIO</PropertyName><Literal>"+n_inventario+"</Literal></PropertyIsEqualTo></Filter>",
        method: "get"
      });
    };
  
    var data = await peticionPoligono(n_inventario);
    if(data.features.length > 0){
      return 'Polygon';
    } else {
      return 'Point';
    }
  }

  /**
   * @public
   * @function
   * @param {Integer} featureID Número de inventario del bien a buscar 
   * @param {String} tipo Tipo de geometría de la geometría a remarcar
   * Función principal que realiza las peticiones y crea el div con los datos a mostrar
   */
  async creaDivConFeature(featureID, tipo){
    var _this = this;
    // Borramos el popup
    if(_this.map_.getPopup() != null) _this.map_.removePopup();

    // Borramos los datos previos si los hubiese, como parcelas anteriores o elementos de capas virtuales como el activo seleccionado
    this.layerToShowWhileHovering.removeFeatures(this.layerToShowWhileHovering.getFeatures());
    this.layerSelectedFeature.removeFeatures(this.layerSelectedFeature.getFeatures());
    this.borraParcela();
    if(this.getCapaPuntos().isVisible()){
      this.getCapaPuntos().setStyle(this.originalStylePoints);
    }
    
    // Borra el div previo si lo hubiese
    if(this.div_ != null){
      this.div_.parentNode.removeChild(this.div_);
      this.div_ = null;
    }

    if(tipo == "Polygon"){
      var url = this.userParams.capa_wfs_bienes + "&filter=<Filter><PropertyIsEqualTo><PropertyName>N_INVENTARIO</PropertyName><Literal>"+featureID+"</Literal></PropertyIsEqualTo></Filter>";
      M.remote.get(url).then(data=>{
        _this.layerSelectedFeature.addFeatures(new M.Feature("featureSelectedInfoWFS", JSON.parse(data.text).features[0]));
      });
    } else {
      if(!this.getCapaPuntos().isVisible()) this.getCapaPuntos().setVisible(true);
      var feat = this.getCapaPuntos().getFeatures().find(x => x.getGeoJSON().properties.N_INVENTARIO == featureID);
      this.pointSelected = feat;
      var iconData = this.userParams.iconoPuntoSeleccionado[feat.getGeoJSON().properties.CODIGO_TIPO_BIEN];
      feat.setStyle(new M.style.Point({
        icon: {
          src: iconData.url,
          scale: iconData.scale
        }
      }));
    }

    // Creamos el div y lo añadimos
    let div = document.createElement("div");
    this.div_ = div;
    this.div_.id = "plg-seleccion-espacial-info-div";
    this.map_.getContainer().appendChild(div);
    
    // Función de Handlebar que formatea la fecha
    Handlebars.registerHelper('formatDate', function(opt){
      if(opt != null && opt.length > 0){
        return opt.substring(8, 10)+"-"+opt.substring(5, 7)+"-"+opt.substring(0, 4);
      } else {
        return '';
      }
    });

    var template = Handlebars.compile(divHTML);
    function bienInmueble(n_inventario){
      return $.ajax({
        url: _this.userParams["apiURL"] + "/bienInmueble/" + n_inventario,
        method: 'get'
      });
    }

    // Creamos las variables de la plantilla
    var context = {};
    context = await bienInmueble(featureID);
    context["n_inventario"] = featureID;
    
    context["urls"] = {
      ficha_prev: this.userParams["ficha"]["prev"],
      ficha_pos: this.userParams["ficha"]["pos"],
      pdf_prev: this.userParams["pdf"]["prev"],
      pdf_pos: this.userParams["pdf"]["pos"]
    };
    this.div_.innerHTML = template(context);

    // Añadimos la lógica para el cambio de pestaña
    $(this.div_).find(".toSelect").click(function(e){
      $(_this.div_).find(".selected").removeClass("selected");
      $(_this.div_).find(".selectedData").removeClass("selectedData");

      var s = $(e.currentTarget);
      s.addClass("selected");

      for(var i = 1; i <= $(_this.div_).find(".dataToShow").length; i++){
        if(s.hasClass("selectable"+i)){
          $(_this.div_).find("#datos").find(".selectable"+i).addClass("selectedData");
          break;
        }
      }
    });

    // Lógica del desplegable en las propiedades de los datos del registro
    function muestraEscondePropiedad(e){
      if($(e).find(".datosRegistroPropiedadTitulo").find("i").first().hasClass("icon-plg-info-activos-plus-solid-1102")){
        $(e).find(".datosRegistroPropiedadTitulo").find("i").first().removeClass("icon-plg-info-activos-plus-solid-1102").addClass("icon-plg-info-activos-minus-solid-1103");
        $(e).find(".datosRegistroPropiedadDatos").show('normal');
      } else {
        $(e).find(".datosRegistroPropiedadTitulo").find("i").first().removeClass("icon-plg-info-activos-minus-solid-1103").addClass("icon-plg-info-activos-plus-solid-1102");
        $(e).find(".datosRegistroPropiedadDatos").hide('normal');
      }
    }

    $(this.div_).find("#datos_registro_propiedad").find(".datosRegistroPropiedadTitulo").click(function(e){
      muestraEscondePropiedad(e.currentTarget.parentElement);
    });

    // Lógica del desplegable en los datos de catastro
    async function muestraEscondeDatosCatastro(e){
      if($(e).find(".datosCatastroTitulo").find("i").first().hasClass("icon-plg-info-activos-plus-solid-1102")){
        $(e).find(".datosCatastroTitulo").find("i").first().removeClass("icon-plg-info-activos-plus-solid-1102").addClass("icon-plg-info-activos-minus-solid-1103");
        $(e).find(".datosCatastroDatos").show('normal');
      } else {
        $(e).find(".datosCatastroTitulo").find("i").first().removeClass("icon-plg-info-activos-minus-solid-1103").addClass("icon-plg-info-activos-plus-solid-1102");
        $(e).find(".datosCatastroDatos").hide('normal');
      }

      // Obtiene la geometría de la parcela
      function getDraw(parcela){
        return $.ajax({
          url: _this.userParams["capa_wfs_parcelas"]+"&service=WFS&version=1.1.0&request=GetFeature&outputFormat=geojson&typename=" + _this.userParams.nombreCapaParcelas + "&Filter=<Filter><PropertyIsEqualTo><PropertyName>ID_DATO_CATASTRO</PropertyName><Literal>"+parcela+"</Literal></PropertyIsEqualTo></Filter>",
          method: "GET"
        });
      }

      // Descarga la parcela y la dibuja
      var parcela = e.getElementsByClassName("dibujoFeature")[0].dataset.parcelaid;
      var DrawGeoJSONObject = new DrawGeoJSON();
      var d = await getDraw(parcela);
      var feature = d["features"][0];
      DrawGeoJSONObject.draw("draw-"+parcela, feature);
      if(_this.parcelaSeleccionada_ != null && parcela == _this.parcelaSeleccionada_)
        $(_this.div_).find("#draw-"+parcela).find("polygon").addClass("active");
    }


    $(this.div_).find("#datos_catastro").find(".datosCatastroTitulo").click(function(e){
      muestraEscondeDatosCatastro(e.currentTarget.parentElement);
    });

    // Lógica del desplegable de la lista de cultivos
    function muestraEscondeCultivos(e){
      $(e).find(".datosCatastroCultivosDatos").toggle();
      if($(e).find(".datosCatastroCultivosTitulo").find("i").first().hasClass("icon-plg-info-activos-plus-solid-1102")){
        $(e).find(".datosCatastroCultivosTitulo").find("i").first().removeClass("icon-plg-info-activos-plus-solid-1102").addClass("icon-plg-info-activos-minus-solid-1103");
      } else {
        $(e).find(".datosCatastroCultivosTitulo").find("i").first().removeClass("icon-plg-info-activos-minus-solid-1103").addClass("icon-plg-info-activos-plus-solid-1102");
      }
    }

    $(this.div_).find("#datos_catastro").find(".datosCatastroCultivosTitulo").click(function(e){
      muestraEscondeCultivos(e.currentTarget.parentElement);
    });

    // Añade o borra la parcela al hacer click en el polígono
    $(this.div_).find("#datos_catastro").find(".muestraParcela").click(function(e){
      if ($(e.currentTarget).find("polygon").hasClass("active")){
        $(e.currentTarget).find("polygon").removeClass("active");
        _this.borraParcela();
      }else{
        $(_this.div_).find("polygon.active").removeClass("active");
        $(e.currentTarget).find("polygon").addClass("active");
        _this.getParcela($(e.currentTarget).data("parcelaid"));
      }
    });

    // Lógica del desplegable de edificios
    function muestraEscondeEdificios(e){
      $(e).find("#datos_edificios").toggle();
      
      if($(e).find("#datos_edificios_titulo").find("i").first().hasClass("icon-plg-info-activos-plus-solid-1102")){
        $(e).find("#datos_edificios_titulo").find("i").first().removeClass("icon-plg-info-activos-plus-solid-1102").addClass("icon-plg-info-activos-minus-solid-1103");
      } else {
        $(e).find("#datos_edificios_titulo").find("i").first().removeClass("icon-plg-info-activos-minus-solid-1103").addClass("icon-plg-info-activos-plus-solid-1102");
      }
    }

    $(this.div_).find("#activos_relacionados").find("#datos_edificios_titulo").click(function(e){
      muestraEscondeEdificios($(_this.div_).find("#activos_relacionados"));
    });

    // Lógica del desplegable de activos relacionados
    function muestraEscondeActivosRelacionados(e){
      $(e).find("#datos_activos_relacionados").toggle();
      
      if($(e).find("#datos_activos_relacionados_titulo").find("i").first().hasClass("icon-plg-info-activos-plus-solid-1102")){
        $(e).find("#datos_activos_relacionados_titulo").find("i").first().removeClass("icon-plg-info-activos-plus-solid-1102").addClass("icon-plg-info-activos-minus-solid-1103");
      } else {
        $(e).find("#datos_activos_relacionados_titulo").find("i").first().removeClass("icon-plg-info-activos-minus-solid-1103").addClass("icon-plg-info-activos-plus-solid-1102");
      }
    }

    $(this.div_).find("#activos_relacionados").find("#datos_activos_relacionados_titulo").click(function(){
      muestraEscondeActivosRelacionados($(_this.div_).find("#activos_relacionados"));
    });

    // Cambio de activo seleccionado clickando en un activo relacionado
    $(this.div_).find("#activos_relacionados").find(".nuevoActivo").click(async function(e){
      _this.creaDivConFeature($(e.currentTarget).data("location"), await _this.getTipoPorId($(e.currentTarget).data("location")));
    });

    $(this.div_).find("#div-boton-mostrar").find("button").click(function(){
      $(_this.div_).removeClass("hidden");
      $(_this.div_).find("#div-boton-mostrar").toggle();
      $(_this.div_).find("#div-datos").toggle();
    });

    //Botón de ocultar
    $(this.div_).find("#div-boton-ocultar").find("button").click(function(){
      $(_this.div_).find("#div-boton-mostrar").toggle();
      $(_this.div_).find("#div-datos").toggle();
      $(_this.div_).addClass("hidden");
    });

    //Botón de cambiar estilos
    $(this.div_).find(".selectChangeStyleSelected").click(()=>{
      if(_this.styleSelectedPolygonType == "fill"){
        _this.styleSelectedPolygon = new M.style.Polygon({
          fill: {},
          stroke: {
            color: 'yellow',
            width: 3
          }
        });
        _this.layerSelectedFeature.setStyle(_this.styleSelectedPolygon);
        _this.styleSelectedPolygonType = "stroke";
      } else {
        _this.styleSelectedPolygon = new M.style.Polygon({
          fill: {
            color: 'yellow'
          }
        });
        _this.layerSelectedFeature.setStyle(_this.styleSelectedPolygon);
        _this.styleSelectedPolygonType = "fill";
      }
    });

    // Botón de centrar a la feature
    $(this.div_).find(".selectZoom").click(()=>{
      var bbox = _this.layerSelectedFeature.getFeaturesExtent()
      if (bbox != null)
        _this.map_.setBbox(_this.layerSelectedFeature.getFeaturesExtent());
      else{
        var center = _this.pointSelected.getGeometry().coordinates;
        _this.map_.setCenter({
          x: center[0],
          y: center[1],
          draw: false
        });
      }
    });

  }

  /**
   * @public
   * @function
   * @param {Integer} idDatoCatastro Id de la parcela en la base de datos.
   * Añade la parcela al mapa en una capa virtual. Borra las anteriores parcelas añadidas
   */
  getParcela(idDatoCatastro){
    this.borraParcela();
    if(idDatoCatastro == null) return;
    this.parcelaSeleccionada_ = idDatoCatastro;
    var _this = this;
    var filter = "&filter=<Filter><PropertyIsEqualTo><PropertyName>ID_DATO_CATASTRO</PropertyName><Literal>"+idDatoCatastro+"</Literal></PropertyIsEqualTo></Filter>";
    M.remote.get(this.userParams.capa_wfs_parcelas+filter).then((data)=>{
      var featuresJSON = JSON.parse(data.text).features;
      if(featuresJSON.length == 0) return;
      var feature = new M.Feature("featureParcelaSelected", featuresJSON[0]);
      _this.layerParcelaSeleccionada.addFeatures(feature);
      _this.map_.setBbox(_this.layerParcelaSeleccionada.getFeaturesExtent());
    });
  }

  /**
   * @public
   * @function
   * Borra las parcelas añadidas al mapa
   */
  borraParcela(){
    this.layerParcelaSeleccionada.removeFeatures(this.layerParcelaSeleccionada.getFeatures());
    this.parcelaSeleccionada_ = null;
  }

}