/**
 * @module M/plugin/PLG_Info_Activo_Patrimonial
 */

import PLG_Seleccion_EspacialControl from './plg_seleccion_espacialcontrol.js';
import PLG_Seleccion_AreaControl from './plg_seleccion_areacontrol.js';
import 'assets/css/plg_seleccion_espacial.css';
import 'assets/css/plg_seleccion_espacial_div.css';
import 'assets/css/plg_seleccion_espacial_popup.css';
import 'assets/css/plg_seleccion_espacial_fonts.css';

import api from '../../api.json';

export default class PLG_Info_Activo_Patrimonial extends M.Plugin {
  
  /**
   * @classdesc
   * Main facade plugin object. This class creates a plugin
   * object which has an implementation Object
   *
   * @constructor
   * @extends {M.Plugin}
   * @param {Object} impl implementation object
   * @param {Object} params
   * @param {String} params.urlConsultaWFS - url del servicio WFS
   * @param {Integer} params.maxArea - área máxima de la selección
   * @param {Object} params.ficha
   * @param {Object} params.pdf
   * @param {String} params.capa_wfs_bienes
   * @param {String} params.capa_wfs_parcelas
   * @param {String} params.apiURL
   * @param {String} params.nombreCapaPuntuales - nombre capa de activos puntuales
   * @param {Array} params.wmsLayers - nombres de las capas con información
   * @param {Object} params.capaATipo - correspondencia nombre de capa y leyenda
   * @param {Object} params.iconoPuntoSeleccionado - iconos correspondientes a las capas
   * @api stable
   */
  constructor(params) {

    super();
    /**
     * Facade of the map
     * @private
     * @type {M.Map}
     */
    this.map_ = null;

    /**
     * Array of controls
     * @private
     * @type {Array<M.Control>}
     */
    this.controls_ = [];

    this.userParams = params;

  }

  /**
   * This function adds this plugin into the map
   *
   * @public
   * @function
   * @param {M.Map} map the map to add the plugin
   * @api stable
   */
  addTo(map) {
    this.controls_.push(new PLG_Seleccion_EspacialControl(this.userParams));
    this.controls_.push(new PLG_Seleccion_AreaControl(this.userParams));
    this.map_ = map;
  
    this.panel_ = new M.ui.Panel("panelPLG_Seleccion_Espacial", {
      collapsible: true,
      position: M.ui.position.TL,
      className: "plg_seleccion_espacial",
      collapsedButtonClass: "g-cartografia-flecha-derecha"
    });

    this.panel_.addControls(this.controls_);
    map.addPanels(this.panel_);
  }

  getMetadata(){
    return api.metadata;
  }
}
